import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { YearSemesterInput, StudentCodeNameInput } from 'components/common/index';

const { Option } = Select;
@Form.create({ name: 'formScholarshipApproval' })
class SearchBar extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <YearSemesterInput form={this.props.form} />
          <StudentCodeNameInput />
          <Col span={6}>
            <Form.Item label="ĐTCS">
              {getFieldDecorator(`objectSubsidy`, {})(
                <Select>
                  <Option value="1">SV thuộc hộ nghèo</Option>
                  <Option value="2">GĐ chính sách</Option>
                  <Option value="3">Mồ côi</Option>
                  <Option value="4">Con thương binh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



