import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Table, Modal } from 'antd';
import { subsidyObject } from 'dump/data';
import { BtnSave, BtnCancel } from 'components/common/button/index';
import SearchBar from './SearchBar';
import './style.scss';

const table1 = [
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Lớp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Đối tượng trợ cấp',
    dataIndex: 'nameObject',
    key: 'nameObject',
  },
];

const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: record.name === 'Disabled User', // Column configuration not to be checked
    name: record.name,
  }),
};

class SubsidyObject extends Component {
  state = { visible: false, type: '' };
  showModal = type => {
    this.setState({
      visible: true,
      type,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };
  render() {
    return (
      <Fragment>
        <Card title="Chọn đối tượng trợ cấp">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                rowSelection={rowSelection}
                bordered
                dataSource={subsidyObject}
                columns={table1}
              />
            </Col>
            <Col span={24} style={{ textAlign: 'center' }}>
              <div className="btn-group">
                <BtnSave onClick={() => this.showModal('link')} title="Xét đối tượng" />
                <BtnCancel onClick={() => this.showModal('unlink')} title="Hủy đối tượng" />
              </div>
            </Col>
          </Row>
          <Modal
            style={{ top: 20 }}
            width="80%"
            title={this.state.type === 'link' ? 'Xét đối tượng' : 'Hủy đối tượng'}
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
          >
            <Row>
              <Col span={24}>
                <Table
                  rowSelection={rowSelection}
                  bordered
                  dataSource={subsidyObject}
                  columns={table1}
                  pagination={false}
                />
              </Col>
            </Row>
          </Modal>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <SubsidyObject />
  </Page>
);



